.tabs {
  display: flex;
}

.tab {
  cursor: pointer;
}

.line {
  width: 100%;
  height: 1px;
  background-color: #ccc; /* Change this to the color you want */
}

.content {
  padding-top: 20px;
}

@media screen and (max-width: 764px)  {

  .tabs{
    overflow: auto;
    padding: 0 14px;
  }
}